<template>
	<view style="height: 100vh;">
		<image class="head" :src="user_info.headimg" mode="aspectFill"></image>
		
		<view class="blank"></view>
		<view class="user_info">
			<image :src="user_info.headimg"></image>
			<text class="username">{{user_info.username}}</text>
			<text class="level">{{user_info.level}}</text>
		</view>
		
		<uni-segmented-control style="margin-top: 3vh;" :current="current" :values="types" @clickItem="onClickItem" style-type="text"
			active-color="red"></uni-segmented-control>
		
		<swiper :current="current" @change="changeSwiper">
			<swiper-item>
				<scroll-view class="scroll2" scroll-y="true">
					<view class="musics">
						<view class="music" v-for="(item, index) in like_musicList" @click="playMusic(item)">
							<image :src="item.picUrl" mode="aspectFit"></image>
							<view class="music_info">
								<text class="music_name">{{item.name}}</text>
								<text class="music_singer">{{item.song.artists[0].name}} • {{item.song.album.name}}</text>
							</view>
						</view>
					</view>
				</scroll-view>
			</swiper-item>
			<swiper-item>
				<scroll-view class="scroll2" scroll-y="true">
					<view class="creates">
						<view class="create" v-for="(item,index) in creates">
							<image :src="item.createImg"></image>
							<text>{{item.createName}} • {{item.createStyle}}</text>
						</view>
					</view>
				</scroll-view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
	export default{
		data() {
			return {
				user_info:{
					headimg:"../static/images/touxiang.jpg",
					username:"小嘟不是小杜",
					level:"创作达人"
				},
				types:["我的喜欢","我的创作"],
				like_musicList:[],
				current:0,
				creates:[
					{
						createImg:"../static/images/styles/liuxing.jpg",
						createName:"创作名1",
						createStyle:"流行"
					},
					{
						createImg:"../static/images/styles/yaogun.jpg",
						createName:"创作名2",
						createStyle:"摇滚"
					},
					{
						createImg:"../static/images/styles/liuxing.jpg",
						createName:"创作名1",
						createStyle:"流行"
					},
					{
						createImg:"../static/images/styles/yaogun.jpg",
						createName:"创作名2",
						createStyle:"摇滚"
					},
					{
						createImg:"../static/images/styles/liuxing.jpg",
						createName:"创作名1",
						createStyle:"流行"
					},
					{
						createImg:"../static/images/styles/yaogun.jpg",
						createName:"创作名2",
						createStyle:"摇滚"
					},
					{
						createImg:"../static/images/styles/liuxing.jpg",
						createName:"创作名1",
						createStyle:"流行"
					},
					{
						createImg:"../static/images/styles/yaogun.jpg",
						createName:"创作名2",
						createStyle:"摇滚"
					},
				]
			}
		},
		mounted() {
			let _this = this;
			uni.request({
				url: "https://autumnfish.cn/personalized/newsong",
				method: "GET",
				success(res) {
					// console.log(res);
					_this.like_musicList = res.data.result;
				}
			});
		},
		methods:{
			changeSwiper(e){
				this.current = e.detail.current;
			},
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
			},
		}
	}
</script>

<style>
	
	.head{
		position: absolute;
		width: 100vw;
		height: 150px;
		z-index: -1;
	}
	
	.blank{
		height: 100px;
	}
	
	.user_info{
		width: 80vw;
		height: 90px;
		margin: 0 auto;
		border: #8F8F94 1px solid;
		border-radius: 20px;
		background-color: #ffffff;
		display: flex;
		flex-direction: row;
		align-items: center;
		box-shadow: 1px 2px 2px 1px #c0c0c0;
	}
	
	.user_info image{
		width: 60px;
		height: 60px;
		border-radius: 30px;
		margin-left: 20px;
	}
	
	.username{
		margin-left: 20px;
		width: 110px;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		overflow: hidden;
	}
	
	.level{
		margin-left: 15px;
		font-size: small;
		color: #0effb7;
	}
	
	swiper {
		height: 60vh;
	}
	
	.scroll2 {
		height: 60vh;
	}
	
	.musics {
		width: 100%;
		margin-top: 5px;
	}
	
	.music {
		height: 45px;
		width: 100%;
		display: flex;
		flex-direction: row;
		background-color: transparent;
		border: white 0;
	}
	
	.music image {
		width: 40px;
		height: 40px;
		border-radius: 8px;
		margin-left: 20px;
	}
	
	.music_info {
		margin-left: 20px;
		display: flex;
		flex-direction: column;
	}
	
	.music_name {
		font-size: 15px;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		overflow: hidden;
	}
	
	.music_singer {
		font-size: 10px;
		color: #a5a5a5;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 1;
		overflow: hidden;
	}
	
	.creates{
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		width: 100vw;
	}
	
	.create{
		width: 28vw;
		height: 35vw;
		margin-left: 4vw;
		margin-top: 5px;
	}
	
	.create image{
		width: 28vw;
		height: 28vw;
		border-radius: 10px;
		box-shadow: 2px 2px 2px 1px #c0c0c0;
	}
	
	.create text{
		font-size: 12px;
		color: #3F536E;
	}
</style>